/* eslint-disable */
export const mockGoods = (): Promise<any[]> => {
  const initData = [
    {
      image: 'https://img.yzcdn.cn/vant/cat.jpeg',
      title:
        '  L115',
      price: '165.00',
    },
    {
      image: 'https://img.yzcdn.cn/vant/cat.jpeg',
      title: 'HLA',
      price: '361.00',
    },
    {
      image: 'https://img.yzcdn.cn/vant/cat.jpeg',
      title:
        'EVA  41-42',
      price: '22.50',
    },
    {
      image: 'https://img.yzcdn.cn/vant/cat.jpeg',
      title: 'T',
      price: '212.00',
    },
  ]
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve(
        new Array(8).fill('').map((_, index) => {
          return {
            index,
            ...initData[index % 4],
            isCutPrice: index % 2 === 0 ? true : false,
          }
        }),
      )
    }, 1400)
  })
}
